const data = JSON.parse(JSON.stringify(kk));
const useStyles = makeStyles((theme) =>({
  root: {
    width: 315,
    margin: 10,
  },
  media: {
    height: 140,
  },
  gridList: {
    width: 500,
    height: 450,
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
    width: 205,
  },
  bb:{
    width: '100%',
  },
  grid: {
    
  },
}));
function Showw(props){
  const classes = useStyles();
return(
  <Grid container className={classes.grid} justify = "center" alignItems = "center">
          {props.value.map((value, index) => {
          return <Card className={classes.root}>
  <CardActionArea>
    <CardMedia
      className={classes.media}
      image={value.image}
      title={value.title}
    />
    <CardContent>
      <Typography gutterBottom variant="h5" component="h2">
      {value.title}
      </Typography>
      <Typography variant="body2" color="textSecondary" component="p">
      {value.about}
      </Typography>
    </CardContent>
  </CardActionArea>
  <CardActions>
    <Button size="small" color="primary">
      Share
    </Button>
    <Button size="small" color="primary">
      Learn More
    </Button>
  </CardActions>
  <CardActions>
    <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>Show links</Typography>
        </AccordionSummary>
        <AccordionDetails>
        <div className={classes.bb}>
          {value.seasons.map((v,indexx) =>{
            return <Accordion>
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography className={classes.heading}>{"Season "+(indexx+1)}</Typography>
            </AccordionSummary>
            <AccordionDetails>
          
            <List className={classes.heading}>
            {v.episodes.map((value, indexxx) => {
          return <ListItem>
             <ListItemText
                    primary={"Episode "+(indexxx+1)}
                    secondary={"test"}
                  />
                  <ListItemSecondaryAction>
                    <IconButton edge="end" aria-label="play" component={Link} to={"/video/"+index+"/"+indexx+"/"+indexxx}>
                      <PlayArrowIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
          </ListItem>
        })}
            </List>
           
            </AccordionDetails>
        </Accordion>
          })}
        </div>
        </AccordionDetails>
    </Accordion>
  </CardActions>
  
</Card>
})}    </Grid>
);
}

function Home(props){
  const classes = useStyles();
  return(<div className="App">
  <header className="App-header">
  </header>
  <body className="body">
  <Showw className="cards" value={data}/>
  </body>
</div>);
}
function Video(){
  let {id,s,e} = useParams();
  return(
    <video controls width="100%">
      <source src={data[id].seasons[s].episodes[e][0]}/>
      <track label="Persian" kind="subtitles" srcLang="fa" src="/bb1.vtt"/>
    </video>
    );
}

function App() {
  
  return (
    
    <Router>
      <Switch>
        <Route exact path="/">
          <Home data={data}/>
        </Route>
        <Route path="/video/:id/:s/:e">
          <Video />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
